<template>
  <div class="equipment">
    <title-header title="批量导入NB设备"></title-header>
    <div class="tools">
      <el-row :gutter="24">
        <el-button class="blue" type="primary" @click="opennbeqment">新增设备</el-button>
        <el-button class="blue" type="primary" @click="download">下载EXCEL模板</el-button>
        <el-button class="blue" type="primary" @click="inputfileclick">导入EXCEL</el-button>
        <el-select v-model="types" class="ml10" placeholder="请选择操作类型">
          <el-option
            v-for="item in NBTypes"
            :key="item.display"
            :label="item.display"
            :value="item.code">
          </el-option>
        </el-select>
      </el-row>
    </div>
    <input type="file" @change="importf($event)" class="inputfile" id="inputfile"/>
    <div id="neweqment" v-show="shownews">
      <div class="pop-content">
          <p class="popup-title">
            新增设备
          </p>
          <div class="popup-con">
            <ul>
              <li class="">
                <span>MAC：</span> <input type="text" v-model="nbeqment.mac" placeholder="请输入设备MAC号码">
                <small style="display: none;">*请输入正确的设备MAC号码</small>
              </li>
              <li class="">
                <span>IMEI：</span> <input type="text" v-model="nbeqment.imei" placeholder="请输入设备IMEI号码">
                <small style="display: none;">*请输入正确的设备IMEI号码</small>
              </li>
              <li class="">
                <span>IMSI：</span> <input type="text" v-model="nbeqment.imsi" placeholder="请输入设备IMSI号码">
                <small style="display: none;">*请输入正确的设备IMSI号码</small>
              </li>
              <li>
                <button class="btn btn-middle cancel" @click="closenbeqment">取消</button>
                <button class="btn btn-middle yes" @click="addnbeqment">确定</button>
              </li>
            </ul>
          </div>
          <span class="layui-layer-setwin">
            <a href="javascript:;" class="close-popup layui-layer-close layui-layer-close1" @click="closenbeqment">
              <span class="iconfont close"></span>
            </a>
          </span>
      </div>
    </div>
  </div>
</template>

<script>
import TitleHeader from '@/components/titleHeader'
import constant from '@/common/constant'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Equipment',
  components: {
    TitleHeader
  },
  data() {
    return {
      types: '',
      NBTypes: Object.keys(constant.NBType).map(k => {
        return {
          code: k,
          display: constant.NBType[k]
        }
      }),
      shownews: false,
      nbeqment: {
        mac: '',
        imei: '',
        imsi: ''
      }
    }
  },
  methods: {
    download() {
      window.location.href = 'http://91mogo.com/download/mac-imei.xlsx'
    },
    opennbeqment() {
      this.shownews = true
      this.nbeqment.mac = ''
      this.nbeqment.imei = '';
    },
    inputfileclick() {
      var inputfile = document.getElementById('inputfile');
      inputfile.click()
    },
    closenbeqment() {
      this.shownews = false
    },
    addnbeqment() {
      // 检查 mac imei值
      const { mac, imei, imsi } = this.nbeqment;
      var maccs = /^[D|d][A-Fa-f0-9]{11}$/;
      var imeics = /^[0-9]{15}$/;
      var imsics = /^[0-9]{15}$/;
      if (!maccs.test(mac)) {
        msgUtil.error('请输入正确格式的mac值')
        return;
      }
      if (!imeics.test(imei)) {
        msgUtil.error('请输入正确格式的imei值')
        return;
      }
      if (!imsics.test(imsi)) {
        msgUtil.error('请输入正确格式的imsi值')
        return;
      }
      var filterMac = this.nbeqments.length && this.nbeqments.filter((option, index) => {
        return option.mac == mac;
      });
      var filterImei = this.nbeqments.length && this.nbeqments.filter((option, index) => {
        return option.imei == imei;
      });
      if (filterMac.length > 0) {
        msgUtil.error('请输入唯一的mac值');
        return;
      }
      if (filterImei.length > 0) {
        msgUtil.error('请输入唯一的imei值');
        return;
      }
      var newngeqment = { 'mac': mac, 'imei': imei, 'imsi': imsi };
      this.nbeqments.push(newngeqment);
      this.shownews = false
    },
    importf(obj) { // 导入
      console.log(obj)
    }
  }
}
</script>

